import BarVertical from "./bar-vertical"
import BarHoriz from "./bar-horiz"
import BarLoop from "./bar-loop"

function createBar(el, opts) {
  opts = opts || {}
  if (opts.xAxis && opts.xAxis.type === 'category') {
    return new BarVertical(el, opts)
  } else if (opts.yAxis && opts.yAxis.type === 'category') {
    return new BarHoriz(el, opts)
  } else if (opts.radiusAxis) {
    return new BarLoop(el, opts)
  }
}

export {
  createBar,
  BarVertical,
  BarHoriz,
  BarLoop
}