<template>
  <div ref="bar" :style="{ width, height }" />
</template>

<script>
import {createBar} from './bar'
export default {
  props: {
    width: {
      type: String,
      default: '600px'
    },
    height: {
      type: String,
      default: '400px'
    },
    options: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      barInstance: null
    }
  },

  watch: {
    options: {
      handler(v) {
        this.update(v)
      },
      deep: true
    }
  },

  mounted() {
    this.barInstance = createBar(this.$refs.bar, this.options)
  },

  destroyed() {
    this.destroy()
  },

  methods: {
    destroy() {
      this.barInstance.destroy()
    },
    update(opts) {
      this.barInstance.update(opts)
    },
    clear() {
      this.barInstance.clear()
    }
  }
}
</script>